:root {
  --white: #f8f8f8;
  --off-white: #f0f0f0;
  --light-gray: #e0e0e0;
  --mid-gray: #a0a0a0;
  --dark-gray: #333333;
  --off-black: #1a1a1a;
  --deep-black: #080808;
  --transition-speed: 0.3s;
}

/* Animation classes */
.fade-in {
  animation: fadeIn 0.5s ease-in;
}

.slide-up {
  animation: slideUp 0.5s ease-out;
}

.float {
  animation: float 6s ease-in-out infinite;
}

.float-slow {
  animation: float 10s ease-in-out infinite;
}

.float-delayed {
  animation: float 8s ease-in-out infinite 1s;
}

.rotate-3d {
  animation: rotate3d 8s linear infinite;
  transform-style: preserve-3d;
}

.pulse-subtle {
  animation: pulseSubtle 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.glow {
  animation: glow 3s ease-in-out infinite alternate;
}

.animate-bounce-slow {
  animation: bounceSlow 3s ease-in-out infinite;
}

.animate-spin-slow {
  animation: spinSlow 15s linear infinite;
}

.animate-reverse-spin-slow {
  animation: spinSlow 20s linear infinite reverse;
}

.perspective {
  perspective: 1000px;
}

/* 3D Cube */
.cube-3d {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  animation: rotateCube 20s linear infinite;
}

.cube-face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: visible;
  opacity: 0.7;
}

.cube-face-front {
  transform: translateZ(150px);
}

.cube-face-back {
  transform: rotateY(180deg) translateZ(150px);
}

.cube-face-right {
  transform: rotateY(90deg) translateZ(150px);
}

.cube-face-left {
  transform: rotateY(-90deg) translateZ(150px);
}

.cube-face-top {
  transform: rotateX(90deg) translateZ(150px);
}

.cube-face-bottom {
  transform: rotateX(-90deg) translateZ(150px);
}

/* 3D Spheres */
.sphere {
  border-radius: 50%;
  background: radial-gradient(circle at 30% 30%, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.1) 30%, rgba(0, 0, 0, 0.1) 80%);
  box-shadow: 
    inset 0 0 20px rgba(255, 255, 255, 0.05),
    0 0 20px rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  opacity: 0.6;
}

.sphere::after {
  content: '';
  position: absolute;
  top: 15%;
  left: 15%;
  width: 25%;
  height: 25%;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0) 70%);
}

.sphere-xs {
  width: 40px;
  height: 40px;
  animation: floatSphere 8s ease-in-out infinite;
}

.sphere-sm {
  width: 80px;
  height: 80px;
  animation: floatSphere 12s ease-in-out infinite;
}

.sphere-md {
  width: 120px;
  height: 120px;
  animation: floatSphere 15s ease-in-out infinite, pulseSphere 10s ease-in-out infinite alternate;
}

.sphere-lg {
  width: 180px;
  height: 180px;
  animation: floatSphere 20s ease-in-out infinite, pulseSphere 15s ease-in-out infinite alternate;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0px); }
}

@keyframes floatSphere {
  0% { transform: translateY(0px) translateX(0px); }
  25% { transform: translateY(-15px) translateX(10px); }
  50% { transform: translateY(5px) translateX(-15px); }
  75% { transform: translateY(-8px) translateX(-8px); }
  100% { transform: translateY(0px) translateX(0px); }
}

@keyframes pulseSphere {
  0% { opacity: 0.3; }
  50% { opacity: 0.7; }
  100% { opacity: 0.3; }
}

@keyframes rotate3d {
  0% { transform: rotateY(0deg); }
  100% { transform: rotateY(360deg); }
}

@keyframes pulseSubtle {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.85; }
}

@keyframes glow {
  0% { box-shadow: 0 0 5px rgba(255, 255, 255, 0.1); }
  100% { box-shadow: 0 0 20px rgba(255, 255, 255, 0.2); }
}

@keyframes bounceSlow {
  0%, 100% { transform: translateY(0) translateX(-50%); }
  50% { transform: translateY(-10px) translateX(-50%); }
}

@keyframes rotateCube {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  25% { transform: rotateX(15deg) rotateY(90deg); }
  50% { transform: rotateX(0deg) rotateY(180deg); }
  75% { transform: rotateX(-15deg) rotateY(270deg); }
  100% { transform: rotateX(0deg) rotateY(360deg); }
}

@keyframes spinSlow {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-track {
  --tw-bg-opacity: 1;
  background-color: rgb(8 8 8 / var(--tw-bg-opacity, 1));
}

::-webkit-scrollbar-thumb {
  --tw-bg-opacity: 1;
  background-color: rgb(26 26 26 / var(--tw-bg-opacity, 1));
}

::-webkit-scrollbar-thumb:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(64 64 64 / var(--tw-bg-opacity, 1));
}

/* 3D Icon styling */
.icon-3d {
  position: relative;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  transform-style: preserve-3d;
  perspective: 800px;
}

.icon-3d svg {
  transform: translateZ(0);
  transition: transform 0.3s ease, filter 0.3s ease;
}

.icon-3d:hover svg {
  transform: translateZ(15px) rotateY(10deg);
  filter: drop-shadow(0 0 8px rgba(255, 255, 255, 0.3));
}

/* Additional animation for skill icons */
@keyframes float-icon {
  0% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
  100% { transform: translateY(0); }
}

.group:hover .icon-3d {
  animation: float-icon 1.5s ease-in-out infinite;
}

/* Floating particles */
.particle {
  pointer-events: none;
  position: absolute;
  border-radius: 9999px;
  opacity: 0.2;
  background: radial-gradient(circle, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0) 70%);
}

.particle-sm {
  height: 0.5rem;
  width: 0.5rem;
}

.particle-md {
  height: 0.75rem;
  width: 0.75rem;
}

.particle-lg {
  height: 1.25rem;
  width: 1.25rem;
}

/* Huge text for sections */
.huge-text {
  font-size: 6rem;
  line-height: 1;
  font-weight: 900;
  color: transparent;
  opacity: 0.05;
}
@media (min-width: 768px) {
  .huge-text {
    font-size: 8rem;
    line-height: 1;
  }
}
.huge-text {
  -webkit-text-stroke: 1px rgba(255,255,255,0.1);
}

/* Skill icons with embossed effect */
.skill-icon-embossed {
  position: relative;
  border-radius: 1rem;
  background-color: rgb(23 23 23 / 0.7);
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  box-shadow: 
    inset 2px 2px 4px rgba(255, 255, 255, 0.1),
    inset -2px -2px 4px rgba(0, 0, 0, 0.5),
    4px 4px 8px rgba(0, 0, 0, 0.4);
  padding: 12px;
}

.skill-icon-embossed:hover {
  transform: translateY(-2px);
  box-shadow: 
    inset 2px 2px 4px rgba(255, 255, 255, 0.15),
    inset -2px -2px 4px rgba(0, 0, 0, 0.6),
    6px 6px 10px rgba(0, 0, 0, 0.5);
}

.skill-icon-embossed img,
.skill-icon-embossed svg {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
  filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
}

.skill-icon-embossed:hover img,
.skill-icon-embossed:hover svg {
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.5)) brightness(1.1);
}

/* Skills Marquee */
.skills-marquee {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 2rem 0;
}

.skills-marquee-track {
  position: relative;
  white-space: nowrap;
}

.skills-marquee-content {
  display: inline-flex;
  animation: marqueeScroll 40s linear infinite;
}

.skills-marquee:hover .skills-marquee-content {
  animation-play-state: paused;
}

@keyframes marqueeScroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

/* Skills */
.skill-item {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1.5rem;
  transition: transform 0.3s ease;
}

.skill-item:hover {
  transform: translateY(-10px);
}

.skill-logo {
  width: 60px;
  height: 60px;
  filter: brightness(1.2) drop-shadow(0 4px 8px rgba(0, 0, 0, 0.15));
  transition: transform 0.3s ease, filter 0.3s ease;
}

.skill-item:hover .skill-logo {
  filter: brightness(1.5) drop-shadow(0 8px 16px rgba(0, 0, 0, 0.25));
  transform: scale(1.1);
}

.skill-item span {
  margin-top: 0.75rem;
  font-size: 0.9rem;
  text-align: center;
}

/* Show All Button */
.show-all-skills {
  margin-top: 2rem;
  background-color: #3b82f6;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.show-all-skills:hover {
  background-color: #2563eb;
}

/* Floating Skills */
.floating-skills-container {
  position: relative;
  width: 100%;
  height: 60vh;
  min-height: 500px;
  overflow: hidden;
}

.skill-item.floating {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation-name: float;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-duration: var(--float-time, 15s);
  animation-delay: var(--float-delay, 0s);
  opacity: 0.8;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.skill-item.floating:hover {
  opacity: 1;
  transform: scale(1.1);
  z-index: 10;
}

.skill-logo {
  width: 60px;
  height: 60px;
  filter: brightness(1.2) drop-shadow(0 4px 8px rgba(0, 0, 0, 0.15));
  transition: transform 0.3s ease, filter 0.3s ease;
}

.skill-item:hover .skill-logo {
  filter: brightness(1.5) drop-shadow(0 8px 16px rgba(0, 0, 0, 0.25));
  transform: scale(1.1);
}

.skill-item span {
  margin-top: 0.75rem;
  font-size: 0.9rem;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.skill-item:hover span {
  opacity: 1;
}

